import { Link } from 'react-router-dom';

const Header = () => {
  return (
    <div className="fixed flex z-[80] items-center justify-between bg-transparent backdrop-blur-sm w-[100%] px-8 py-3 top-0">
      <div className="flex items-start gap-x-4">
        <a href="mailto:azddoumaryam@gmail.com">
          <button className="inline-flex items-center justify-center text-sm font-medium ring-offset-background transition-colors focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 bg-[#D0FC35] hover:bg-[#D0FC35]/80 h-9 rounded-md px-5 py-6">
            Let's get in touch
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="24"
              height="24"
              viewBox="0 0 24 24"
              fill="none"
              stroke="currentColor"
              strokeWidth="2"
              strokeLinecap="round"
              strokeLinejoin="round"
              className="lucide lucide-mouse-pointer ml-2 h-5 w-5"
            >
              <path d="m3 3 7.07 16.97 2.51-7.39 7.39-2.51L3 3z"></path>
              <path d="m13 13 6 6"></path>
            </svg>
          </button>
        </a>
      </div>
      <Link className='text-[#666666] transition-all duration-300 hover:text-[#D0FC35]' to="/">Blog</Link>
    </div>
  );
};

export default Header;
