"use client";
import React from "react";
import { SparklesCore } from "../components/ui/sparkles.tsx";
import { Spotlight } from "../components/ui/spotlight.tsx";
import { HeroParallax } from "../components/ui/hero-parallax.tsx";
const FrontEndInfo = () => {
  return (
    <>
      <div className="h-full pt-24 w-full dark:bg-black flex flex-col items-center justify-center overflow-hidden rounded-md">
        <Spotlight
          className="-top-40 left-0 md:left-60 md:-top-20"
          fill="white"
        />

        <h1 className="md:text-8xl text-6xl lg:text-[12rem] font-bold text-center dark:text-white relative z-20">
          Frontend
        </h1>
        <div className="w-[40rem] h-40 relative">
          {/* Gradients */}
          <div className="absolute inset-x-20 top-0 bg-gradient-to-r from-transparent via-indigo-500 to-transparent h-[2px] w-3/4 blur-sm" />
          <div className="absolute inset-x-20 top-0 bg-gradient-to-r from-transparent via-indigo-500 to-transparent h-px w-3/4" />
          <div className="absolute inset-x-60 top-0 bg-gradient-to-r from-transparent via-sky-500 to-transparent h-[5px] w-1/4 blur-sm" />
          <div className="absolute inset-x-60 top-0 bg-gradient-to-r from-transparent via-sky-500 to-transparent h-px w-1/4" />

          {/* Core component */}
          <SparklesCore
            background="transparent"
            minSize={0.4}
            maxSize={1}
            particleDensity={1200}
            className="w-full h-full"
            particleColor="#FFFFFF"
          />

          {/* Radial Gradient to prevent sharp edges */}
          <div className="absolute inset-0 w-full h-full dark:bg-black [mask-image:radial-gradient(350px_200px_at_top,transparent_20%,white)]"></div>
        </div>
        <HeroParallax products={products} />
      </div>
    </>
  );
};
export default FrontEndInfo;
